import { useDispatch } from "react-redux";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { Form } from "./form";
import { Link, useNavigate } from "react-router-dom"
import { setUser } from "store/slices/userSlice";

const Login = () => {
   const dispatch = useDispatch();
   const navigate = useNavigate();
   const handleLogin = (email, password) => {

      const auth = getAuth();
      signInWithEmailAndPassword(auth, email, password)
         .then(({ user }) => {
            dispatch(setUser({
               email: user.email,
               id: user.uid,
               token: user.accessToken,
            }));
            navigate('/registrationForm/');
         })
         .catch((error) => {
            if (error.message === 'Firebase: Error (auth/wrong-password).') alert('Неверный пароль, попробуйте ещё раз')
            if (error.message === 'Firebase: Error (auth/user-not-found).') alert('Такой аккаунт не зарегистрирован')

            console.log(error.message)
         })
   }

   return (
      <Form
         title="Вход"
         btnTitle="Войти"
         handleSubmit={handleLogin}
         element={<p className="register"><Link to="/registrationForm/register">Зарегистрироваться?</Link></p>}
      />

   )
}

export { Login }