import { useState } from "react";
import { useForm } from "react-hook-form";

const RegisterForm = ({ title, description, btnTitle, element, submit }) => {


   const [toggleIcon, setToggleIcon] = useState('🔒');
   const [toggleIconClasses, setToggleIconClasses] = useState('toggle-icon-passive');
   const [passLabel, setPassLabel] = useState('');
   const [type, setType] = useState('password');
   const [percentBar, setPercentBar] = useState('none');

   const togglePassInput = (e) => {
      if (type === 'password') {
         setType('text');
         setToggleIcon('🔓');
         setToggleIconClasses('toggle-icon-active');
      } else {
         setType('password');
         setToggleIcon('🔒');
         setToggleIconClasses('toggle-icon-passive');
      }
   };

   const addClass = (className) => {
      setPercentBar('');
      if (className) {
         setPercentBar(className);
      }
   };

   // const handlePassInput = (e) => {

   //    if (e.target.value.length === 0) {
   //       setPassLabel('');
   //       addClass('none');
   //    } else if (e.target.value.length <= 4) {
   //       setPassLabel('Не надежный пароль');
   //       addClass('weak');
   //    } else if (e.target.value.length <= 7) {
   //       setPassLabel('Средний пароль');
   //       addClass('average');
   //    } else {
   //       setPassLabel('Надежный пароль');
   //       addClass('strong');
   //    }
   // };


   const {
      register,
      watch,
      formState: {
         errors,
         isValid
      },
      handleSubmit,
      reset,
   } = useForm({
      mode: ''
   });

   const onSubmit = (data) => {
      //alert(JSON.stringify(data));
      const email = data.email;
      const pass = data.password;
      localStorage.setItem('name', data.name);
      addClass('none');
      setPassLabel('');
      reset();
      submit(email, pass);

   }

   return (
      <form className="form" onSubmit={handleSubmit(onSubmit)}>
         <p>{title}</p>
         <p className="fz_16 mb_15">{description}</p>
         <label className="form__label name">
            {errors?.name && <span className="error">{errors?.name?.message || 'Error'}</span>}
            <input
               className="form__name"
               placeholder="Имя"
               {...register('name', {
                  required: 'Введите имя',
                  minLength: {
                     value: 2,
                     message: 'Имя пользователя не менее 2-х символов'
                  },
                  maxLength: {
                     value: 15,
                     message: 'Имя пользователя не более 15-и символов'
                  }
               })}
            />
         </label>
         <label className="form__label surname">
            {errors?.surname && <span className="error">{errors?.surname?.message || 'Error'}</span>}
            <input
               className="form__name"
               placeholder="Фамилия"
               {...register('surname', {
                  required: 'Введите фамилию',
                  minLength: {
                     value: 2,
                     message: 'Фамилия не менее 2-х символов'
                  },
                  maxLength: {
                     value: 15,
                     message: 'Фамилия не более 15-и символов'
                  }
               })}
            />
         </label>
         <label className="form__label phone">
            {errors?.phone && <span className="error">{errors?.phone?.message || 'Ошибка ввода'}</span>}
            <input
               className="form__phone"
               placeholder="Телефон"
               {...register('phone', {
                  required: 'Введите номер телефона',
                  pattern: {
                     value: /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/,
                     message: 'Номер телефона введен не верно'
                  },
               })}
            />
         </label>
         <label className="form__label email">
            {errors?.email && <span className="error">{errors?.email?.message || 'Ошибка ввода'}</span>}
            <input
               className="form__email"
               placeholder="E-mail"
               {...register('email', {
                  required: 'Введите email',
                  pattern: {
                     value: /^((([0-9A-Za-z]{1}[-0-9A-z\.]{1,}[0-9A-Za-z]{1})|([0-9А-Яа-я]{1}[-0-9А-я\.]{1,}[0-9А-Яа-я]{1}))@([-0-9A-Za-z]{1,}\.){1,2}[-A-Za-z]{2,})$/u,
                     message: 'Email введен не верно'
                  },
               })}
            />
         </label>
         <label className="form__label pass">
            {errors?.password && <span className="error">{errors?.password?.message || 'Ошибка ввода'}</span>}
            <div className="input-group">
               <input
                  className="form__pass"
                  placeholder="Пароль"
                  autoComplete="off"
                  type={type}
                  {...register('password', {
                     required: 'Введите пароль',
                     minLength: {
                        value: 6,
                        message: 'Пароль должен быть не меньше 6 символов!'
                     },
                     validate: (value) => {
                        if (value.length === 0) {
                           return (
                              setPassLabel(''),
                              addClass('none')
                           )
                        } else if (value.length <= 4) {
                           return (
                              setPassLabel('Не надежный пароль'),
                              addClass('weak')
                           )
                        } else if (value.length <= 7) {
                           return (
                              setPassLabel('Средний пароль'),
                              addClass('average')
                           )
                        } else {
                           setPassLabel('Надежный пароль');
                           addClass('strong');
                        }
                     }
                  })}
               />
               <span
                  onClick={togglePassInput}
                  className={`toggle ${toggleIconClasses}`}
               >
                  {toggleIcon}
               </span>
            </div>
            <div className='pass-strength'>
               <div className='strength-percent ml_15'>
                  <span className={percentBar}></span>
               </div>
               <span className='strength-label'>{passLabel}</span>
            </div>
         </label>
         <label className="form__label confirmPass">
            {errors?.confirmPass && <span className="error">{errors?.confirmPass?.message || 'Ошибка ввода'}</span>}
            <input
               className="form__confirmPass"
               placeholder="Повторите пароль"
               autoComplete="off"
               type={type}
               {...register('confirmPass', {
                  required: 'Введите пароль',
                  validate: (value) => {
                     if (watch('password') != value) {
                        return "Пароли не совпадают!";
                     }
                  },
               })}
            />
         </label>
         <button className="form__submit" type="submit">{btnTitle}</button>
         <hr />
         {element}
      </form>
   );
}

export { RegisterForm }